import React from 'react'
import Img from 'gatsby-image';
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

const Apropos = (props) => (
    <div id="about">
        <Helmet>
            <title> propos de Webjomp</title>
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1> propos de Webjomp</h1>
                    </header>
                    <span className="image left"><img src={props.data.file.childImageSharp.responsiveResolution.src} alt="Franois-Xavier Lemire" /></span>
                    <p>
                        Fondateur de Webjomp, Franois-Xavier Lemire obtient un diplme en informatique avec une majeure en gnie logiciel  l'Universit McGill en 2016. Il est aussi diplm en droit de l'Universit de Montral depuis 2010 et a t membre du Barreau du Qubec de 2011  2025, auparavant praticien au sein du cabinet <a href="http://www.lemireavocat.com">Lemire Lemire avocats, s.e.n.c.</a>
                    </p>
                    <p>
                        Franois-Xavier a dvelopp ses talents d'ingnieur logiciel en touchant  plusieurs disciplines, dont la <i>FinTech</i> chez <a href="http://www.morganstanley.com/">Morgan Stanley</a>, les jeux vido (<i>Skylanders : SuperChargers</i>) chez <a href="http://beenox.com/">Activision</a>, et la publicit mobile chez <a href="https://vungle.com/">Vungle</a>,  San Francisco.
                    </p>
                    <p>
                        S'il n'est pas en train de rflchir  sa prochaine application, il est soit en train de pratiquer la trompette, de dguster un bon scotch ou bien de boxer!
                    </p>
                    <p>
                        N'hsitez pas  rester connect!

                        <ul className="icons inline">
                            <li><a href="https://twitter.com/fxlemire" className="icon alt fa-twitter"><span className="label">Twitter</span></a></li>
                            <li><a href="https://www.linkedin.com/in/fxlemire/" className="icon alt fa-linkedin"><span className="label">LinkedIn</span></a></li>
                            <li><a href="https://github.com/fxlemire" className="icon alt fa-github"><span className="label">GitHub</span></a></li>
                        </ul>
                    </p>
                </div>
            </section>
        </div>

    </div>
)

export default Apropos

export const pageQuery = graphql`
    query GatsbyImageQuery {
        file(relativePath: { eq: "fx.png" }) {
            childImageSharp {
                responsiveResolution {
                    src
                }
            }
        }
    }
`



